import React from "react";
import { useState, useEffect } from "react";
import "./styles.css";
import { Chart } from "react-google-charts";

function App() {
  const [todo, setTodo] = React.useState(null);
  const [todo2, setTodo2] = React.useState({});
  const [todo3, setTodo3] = React.useState({});
  const [todo4, setTodo4] = React.useState({});
  const [todo5, setTodo5] = React.useState({});
  const [todo6, setTodo6] = React.useState({});
  const [todo7, setTodo7] = React.useState({});
  const [id, setId] = React.useState(1);

  React.useEffect(() => {
    if (id == null || id === "") {
      return;
    }

    fetch(`https://pokeapi.co/api/v2/pokemon/${id}`)
      .then((results) => results.json())
      .then((data) => {
        setTodo(data.sprites.front_default);
      });
    fetch(`https://pokeapi.co/api/v2/pokemon/${id}`)
      .then((results) => results.json())
      .then((data) => {
        setTodo2(data.name);
      });
    fetch(`https://pokeapi.co/api/v2/pokemon/${id}`)
      .then((results) => results.json())
      .then((data) => {
        setTodo3(Object.values(data.sprites));
      });
    fetch(`https://pokeapi.co/api/v2/pokemon/${id}`)
      .then((results) => results.json())
      .then((data) => {
        setTodo4(data.base_experience);
      });
    fetch(`https://pokeapi.co/api/v2/pokemon/${id}`)
      .then((results) => results.json())
      .then((data) => {
        setTodo5(data.weight);
      });
    fetch(`https://pokeapi.co/api/v2/pokemon/${id}`)
      .then((results) => results.json())
      .then((data) => {
        setTodo6(data.height);
      });
    fetch(`https://pokeapi.co/api/v2/pokemon/${id}`)
      .then((results) => results.json())
      .then((data) => {
        setTodo7(data.types[0].type.name);
      });
  }, [id]); // useEffect will trigger whenever id is different.

  const data = [
    ["Element", "Density", { role: "style" }],
    ["Base Exp", todo4, "#b87333"], // RGB value
    ["Weight", todo5, "silver"], // English color name
    ["Height", todo6, "gold"]
  ];

  const outputName = Object.values(todo2);

  return (
    <>
      <div className="App">
        <div className="window">
          <h4>Pokemon App created by Fernando</h4>
          <br />
          <p> Enter pokemon number or name into Pokedex</p>
          <input value={id} onChange={(e) => setId(e.target.value)} />

          <h1>{outputName}</h1>
          <h3>{Object.values(todo7)}</h3>

          <img src={todo} width="200" px height="200" px />
          <img src={todo3[0]} width="200" px height="200" px />
          <img src={todo3[6]} width="200" px height="200" px />
        </div>
      </div>

      <Chart chartType="ColumnChart" data={data} width="100%" height="400px" />
    </>
  );
}

export default App;
